.cart {
    &__form {
        display: flex;
        justify-content: space-between;
        flex-flow: row wrap;
        align-items: baseline;
        margin: 20px 0;

        &-field {
            padding: 10px 20px;
            margin: 0 20px 10px 0;
            border-radius: 10px;
            border: 1px solid #ccc;
            width: 100%; }

        &-fields {
            display: flex;
            flex-direction: column;
            margin: 0 60px 0 0;
            max-width: 400px;
            width: 100%; } } }
